
.container-wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    
}
.card-wrapper {
display: flex;
flex-wrap: wrap;
justify-content: center;
align-items: center;


}

.single-card {
    transition: transform 0.6s;
    transform-style: preserve-3d;
    position: relative;
    cursor: pointer;
    width: 350px;
    height: 400px;
    margin: 10px;
    box-shadow: 0 2px 10px 5px rgba(0, 0, 0, 0.2);
    border-radius: 20px;
    
    &.flip {
        transform: rotateY(180deg);
    }

    @media(max-width: 500px){
        max-width: 310px;
    }
}